@import url(https://fonts.googleapis.com/css2?family=Glory:wght@400;500&display=swap);
.App {
  margin: 0;
  padding: 30px;
  background-color: rgb(241, 250, 241);
  font-family: 'Glory', sans-serif;
}

li {
  list-style: none;
}

.profile {
  width: 250px;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.description {
  margin: auto;
  text-align: center;
}

.avatar {
  display: flex;
  border-radius: 50%;
  margin: auto;
  padding-bottom: 15px;
}

.name {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
  text-align: center;
  padding-bottom: 15px;
}

.tag {
  margin: 0;
  padding-bottom: 15px;
}

.location {
  margin: 0;
  padding-bottom: 15px;
}

.stats {
  display: flex;
  justify-content: space-between;
  background-color: rgb(245, 244, 243);
  padding: 0;
  border: 1px solid rgb(245, 244, 243);
}

.stats-item {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  width: calc(100% / 3);
  padding: 15px;
  border: 1px solid #ececec;
}

.statistics {
  margin: auto;
  width: 250px;
  text-align: center;
}

.stat-list {
  display: flex;
  padding: 0;
}

.stat-item {
  display: flex;
  width: calc(100% / 5);
  flex-direction: column;
  border: 1px solid #ececec;
  padding: 10px;
}

.friend-list {
  width: 250px;
  margin: auto;
  padding: 15px;
}

.item {
  display: flex;
  padding: 15px;
  align-items: center;
  border: 1px solid #ececec;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.25);
}
.status {
  background-color: red;
  width: 15px;
  height: 15px;
  border-radius: 50%;
}

.status.isOnline {
  background-color: green;
}
.status::before {
  display: flex;
  content: '';
}

.friend-avatar {
  border-radius: 10px;
  margin-left: 15px;
  margin-right: 15px;
}

.transaction-history {
  width: 100%;
  margin: auto;
}

.transaction-head-item {
  padding: 15px;
  background-color: rgb(90, 183, 236);
  text-transform: uppercase;
}

.transaction-body-item {
  height: 36px;
  text-align: center;
  border: 1px solid #ececec;
  background-color: #ececec;
  text-transform: capitalize;
}

